.input-field {
    height: 4rem;
    width: 30rem;
    padding: 1.3rem 2rem;
    border: 1px solid #555;
    border-radius: 7px;
  }
  
  .label-field {
    margin: 1.2rem;
    font-size: 1.4rem;
    font-weight: 500;
    color: #000;
  }
  
  .formInput{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    padding: 1rem;
  }
  
  
  span {
    font-size: 1.4rem;
    padding: 0.5rem 2rem;
    color: red;
    display: none;
  }
  
  input:invalid[focused="true"] {
    border: 1px solid red;
  }
  
  input:invalid[focused="true"] ~ span {
    display: block;
  }
  
  