   /* Reset default margin and padding */
body {
  margin: 0;
  padding: 0;
}

/* Set width to 100% for .about-us and .adopt-flex */
.about-us, .adopt-flex {
  width: 100%;
}

/* Other styles remain the same */
.about-us {
  margin-bottom: 5.6rem;
}

.left-col {
  /* bac colr */
  
  /* background-color: #fed201; */
  background-color: #d8bfd8;
  padding: 1rem 2rem;
}

.right-col {
  background: url(../../public/assets/img/about.jpg) no-repeat center;
  background-size: cover;
  min-height: 42.8rem;
}

.article-paragraph {
  font-size: 1.6rem;
  line-height: 1.5;
}

.adopt-flex {
  /* background: #1a75bc; */
  background: lightblue;
  display: flex;
  align-items: center;
  max-height: 50.6rem;
  margin-bottom: 9.6rem;
  margin-top: 5rem;
}

.img-holder {
  position: relative;
}

.img-responsive {
  position: absolute;
  right: 0;
  top: 3rem;
  height: 25rem;
}

.breeds-wrapper-title {
  font-size: 4.6rem;
  text-transform: capitalize;
  color: #000;
  margin-bottom: 1.2em;
  font-weight: 600;
  text-align: center;
  position: relative;
}