.wrapper-form {
  background: url(../../../public/assets/img/section.jpg) no-repeat center 0px;
  background-size: cover;
  min-height: 100rem;
  object-fit: contain;
  margin-bottom: 5.6rem;
}

.form{
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 4rem;
  
}

.field-form {
display: flex;
flex-direction: column;
align-items: center;
} 

.input-field {
height: 4rem;
width: 30rem;
padding: 1.3rem 2rem;
border: 1px solid #555;
border-radius: 7px;
}

.label-field {
margin: 1.2rem;
font-size: 1.4rem;
font-weight: 500;
}


.submit-button{
padding: 0.6rem 2.4rem;
margin-bottom: 2.4rem;
cursor: pointer;
font-size: 1.8rem;
font-weight: 500;
width: 17.3rem;
border-radius: 9px;
border: none;
color: #555;
background-color: #fab005;

transition: all 0.5s;
}

.space {
height: 10rem;
}

.btn-form-container{
grid-column: span 2;
margin-top: 6rem;
text-align: center;
}

